.tableSection {
  position:relative;
}
table, th, td {
    margin-top: 50px;
    border: 1px solid black;
    border-collapse: collapse;
  }

  th {
    padding: 5px;
    background-color: #3b7593;
  }

  td {
    padding: 5px;
  }

  tr:hover {
    background-color: #323a1a;
  }


  .buttons button.active {
    background-color: white;
    color: #395bab;
    border-color: #1e8b97;
  }

  .buttons button:disabled {
    opacity: 0.4;
  }